html {
  scroll-behavior: smooth;
}

body {
  margin: 0 0;
  padding: 0;
  max-width: 100vw;
  font-family: "Lato", sans-serif;
}

p {
  font-family: "Poppins", sans-serif;
}

header,
section,
footer {
  padding: 0 20px;

  @media (min-width: 1250px) {
    padding: 0 100px;
  }
}


